
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import SelectTab from "components/SelectTab/selectTab";
export default {
data() {
return {
noData: false,
TabDefault1:{key:'1,2,3,4',val:'全部类型'},
TabDefault2:{key:'0',val:'时间筛选'},
select1: [{key:'1,2,3,4',val:'全部'},{key:'1,2',val:'产前随访'},{key:'3,4',val:'产后随访'},],
select2: [{key:'0',val:'全部'},{key:'1',val:'近三月'},{key:'2',val:'近一年'},{key:'3',val:'近三年'}],
type: '1,2,3,4',
date: '0',
pageIndex: 1,
pageSize: 15,
list: [],
};
},
mounted() {

this.$refs.scroll.$el.style.height = document.body.clientHeight-124+'px';
this.getFollowUpList(true);
},
methods: {

selChange1(obj) {
this.type = obj.key;
this.getFollowUpList(true);
},

selChange2(obj) {
this.date = obj.key;
this.getFollowUpList(true);
},

getFollowUpList(upOrDown) {
if (upOrDown) {
this.pageIndex = 1;
}
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "suifangService",
"MSH.2": "list",
'GRDAID':this.$root.loginInfo.grdaid,
"CHZBM": this.$root.loginInfo.CHZBM,
"type": this.type,
"date": this.date,
"pageIndex": this.pageIndex,
"pageSize": this.pageSize,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.pageNumber == 1) {
this.pageIndex = 1,
this.list = [];
}
if (res.rows.length > 0) {
this.noData = false;
this.pageIndex++;
this.list = [...this.list,...res.rows];
} else {
if (res.pageNumber > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.noData = true;
}
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
},

gotoDetail(TYPE, ID) {
if (TYPE == "1") {
this.$router.push({name: "descAntenatal1", params: {ID: ID}});
} else if (TYPE == "2") {
this.$router.push({name: "descAntenatal2", params: {ID: ID}});
} else if (TYPE == "3") {
this.$router.push({name: "descPostpartum", params: {ID: ID}});
} else if (TYPE == "4") {
this.$router.push({name: "descPostpartum42", params: {ID: ID}});
}
},

titleString(TYPE, SORT) {
if (TYPE == "1") {
return "第1次产前随访服务记录表";
} else if (TYPE == "2") {
return "第" + SORT + "次产前检查服务记录表";
} else if (TYPE == "3") {
return "产后访视记录表";
} else if (TYPE == "4") {
return "产后42天健康检查记录表";
}
}
},
components: {
XHeader,
SelectTab,
InlineLoading
}
};



